	module.exports = {"data":{"markdownRemark":{"html":"<p>Following <a href=\"http://eddielee.me/its-been-a-year-a-retrospective/\">last years post</a>, I've decided to do another year in review, and 2017 has been one crazy year!</p>\n<p><strong>Family</strong></p>\n<p><img src=\"/images/2017-review-house.jpg\" alt=\"Our house\">\nThe overwhelming highlight of this year has been buying and moving into our first family home. But this wasn’t the only crazy change or achievement the year had to offer. Lauren got a new job, I got promoted, Ethan started School, Aurora started Nursery, and we got our first car.</p>\n<p><strong>Fun</strong></p>\n<p><img src=\"/images/2017-review-family.jpg\" alt=\"Our family\">\nWith all this it's surprising we had any time for fun, but along with our summer holiday and numerous trips to Thomas Land we managed quite a lot. I even managed to see a whole load of my favourite bands; Blink 182, Green Day, Weezer and Good Charlotte - all in one year.</p>\n<p><strong>Apps</strong></p>\n<p>At the end of last year Glass Umbrella committed to <a href=\"https://twitter.com/glassumbrellaco/status/815270013808607232\">release an app in 2017</a>, well that <a href=\"https://twitter.com/glassumbrellaco/status/947554224103198720\">didn’t happen</a>. However, I can share what I’ve been working on for the last year. Drank is your personal window into understanding the makeup of your daily fluid intake; helping you keep a healthy balance of water, caffeine and alcohol. After a bit of a tidy up and some design work, the app should ship early in 2018.  Until then there is a beta you can join via Twitter DM to <a href=\"https://twitter.com/thedrankapp\">@thedrankapp</a>.\n<img src=\"/images/2017-review-app.png\" alt=\"Drank App\"></p>\n<p>Here's looking forward to all 2018 brings, hopefully a bit less change, a lot more relaxing and family time.</p>","timeToRead":1,"excerpt":"Following  last years post , I've decided to do another year in review, and 2017 has been one crazy year! Family \nThe overwhelming highlight…","frontmatter":{"title":"2017 a year in review","cover":"https://unsplash.it/1152/300/?random?2017","date":"2017-12-31T23:07","tags":["personal","family"],"author":"eddie"},"fields":{"slug":"/2017-a-year-in-review"}},"prev":{"excerpt":"","frontmatter":{"title":"There is","cover":"https://unsplash.it/1152/300/?random?sad","date":"2018-05-08T22:44"},"fields":{"slug":"/there-is"}},"next":{"excerpt":"Reaction Match version 2.2 is now out; with support for iPhone X, updated to Swift 4 and a few bug fixes for…","frontmatter":{"title":"Reaction Match iPhone X update","cover":"https://unsplash.it/1152/300/?random?iPhoneX","date":"2017-12-10T09:24"},"fields":{"slug":"/reaction-match-iphone-x-update"}},"authors":{"edges":[{"node":{"id":"eddie","name":"Eddie Lee","image":"/images/eddielee.jpg","url":"https://twitter.com/eddielee6","bio":"Father and Software Engineer"}}]}},"pathContext":{"slug":"/2017-a-year-in-review","total":51,"prev":"/there-is","next":"/reaction-match-iphone-x-update"}}

